@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300;500&family=Merriweather&display=swap);
body {
  margin: 0px;
  padding: 0px;
}

.ql-font-roboto {
  font-family: "Jost", sans-serif !important ;
}

.ql-syntax { 
  background-color:black; 
  color  : white ;
  border-radius: 10px ;
  padding : 10px 20px ; 
}
.post-container { 
    width: 80% ;
    max-height: auto ; 
    min-height: 300px;
    margin: 0px auto ; 
    /* background-color:yellow ;  */
    display: flex ; 
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
}
.ql-container.ql-snow {
  width: 100%;
  border: none;
  background-color: rgb(247, 247, 247);
  margin: 0px auto;
  overflow: scroll ;
  min-height: 200px;
  max-height: 60vh ;
 
  
}

.ql-container.ql-snow::-webkit-scrollbar {
  display: none;
}

.ql-container p {
  font-size: 18px;

  font-family: "Roboto", serif;
}

.ql-container blockquote {
  font-size: 15px;

  font-family: "Roboto", serif;
  font-style: italic;
}

.ql-container h1 {
  font-size: 30px;
  font-family: "Montserrat", sans-serif !important ;
}

.ql-container h2 {
  font-size: 27px;
  font-family: "Montserrat", sans-serif !important ;
}

